* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to left, #fefcea, #f1da36);
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.input-style{
  border-color: orange;
 
}


input:hover,
input:focus {
border-color: green;
  
}


.flag {
  width: 130px;
    height: 90px;
}

.list {
  list-style: none;
}

.span{
  margin-right: 7px;
}

.font-size {
  font-family: monospace;
  font-size: larger;
}

.country-name {
  font-weight: bold; 
  height: 30px;
  
}